import {colors} from '../utils/styles'
import {img} from '../utils/images'
import Link from 'next/link'
import {Affix, Button, Menu, Dropdown, Icon} from 'antd';
import 'antd/dist/antd.css'

const styles = {
    registrationButton: {
        borderRadius: '50px',
        margin: '0 40px 0 20px'
    },
    menuButton: {
        borderRadius: '50px'
    },
    navigationWrapper: {
        color: colors.primary.dark,
        display: 'flex',
        flexDirection: 'row',
        alignItems: 'center',
        justifyContent: 'space-between',
        height: 80,
        width: '100%',
        backgroundColor: 'white',
        top: 0,
        boxShadow: '0px 3px 4px -2px rgba(117,117,117,1)',
        zIndex: 100
    },
    navLogo: {
        height: 30,
        marginLeft: 40
    }
}

const menu = (
    <Menu>
        <Menu.Item>
            <Link href='/'>
                <a rel='noopener noreferrer'>Домашняя страница</a>
            </Link>
        </Menu.Item>
        <Menu.Item>
            <Link href='/program'>
                <a rel="noopener noreferrer">Программа форума</a>
            </Link>
        </Menu.Item>
        <Menu.Item>
            <Link href='/registration'>
                <a rel="noopener noreferrer">Регистрация</a>
            </Link>
        </Menu.Item>
        <Menu.Item>
            <Link href='/projects'>
                <a rel="noopener noreferrer">Проекты</a>
            </Link>
        </Menu.Item>
        <Menu.Item>
            <Link href='/contacts'>
                <a rel="noopener noreferrer">Контакты</a>
            </Link>
        </Menu.Item>
    </Menu>
);

export default() => {
    return (
        <Affix offsetTop={0}>
            <div style={styles.navigationWrapper}>
                <img
                    src={img.yfnyLogoMark.url}
                    alt={img.yfnyLogoMark.alt}
                    style={styles.navLogo}/>
                <div>
                    <Dropdown overlay={menu}>
                        <Button type='primary' ghost style={styles.menuButton}>
                            Навигация
                            <Icon type="down"/>
                        </Button>
                    </Dropdown>
                    <Button type='primary' style={styles.registrationButton}>Регистрация</Button>
                </div>

            </div>
        </Affix>
    )
}



